<template>
  <div class="restrict research">
    <div class="row">
      <div class="maxTitle">
        <div>RESEARCH AREA</div>
      </div>
      <ul class="researchlist">
        <li v-for="i in research_list" :key="i.id">
          <!-- <router-link :to="'/pdf/'+i.id" target="_blank">
            {{ i.id + '. ' + i.title }}</router-link
          > -->
          <a :href="i.src">{{ i.id + '. ' + i.title }}</a>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import { getDocument } from "@/api/video/index.js"
export default {
	data: () => ({
		research_list:[
		]
	}),
	beforeMount() {
		getDocument().then(res => {
			this.research_list = res.data.map(v => ({
				id:v.id,
				title:v.title,
				src: v.url
			}))
		}).catch(err => {
			console.log(err)
		})
	}
};
</script>
